import DataService from 'dataService';
import _ from 'underscore';

class Controller {
  constructor($scope, $timeout, cachePool) {
    this.$scope = $scope;
    this.$timeout = $timeout;
    this.userData = cachePool.pull('user');

    $scope.showDate = function(){
      $(".dropdown-date").toggle();
    }
    $scope.beforeRender = function ($view, $dates, $leftDate, $upDate, $rightDate) {
        var now = Date.now();
        $dates.forEach(date => {
          if(date.localDateValue() <= now){
              date.selectable = false;
          }
        })
    }

    $scope.params = {
      option1:"",
      option2:"",
      id: 0,
      score: "20",
      stop_time: new Date()
    }
    //开奖选项
    $scope.finishParams = null;
    $scope.finishTime = "";
    //0:进行中, 1:已封盘，2:已开奖, 3:已流盘
    $scope.status = 0;
    $scope.isCreate = true;
    $scope.can_edit = true;
    $scope.autoPred = false;
    $scope.editRight = false;
    $scope.right_option = "1";

    this.getPred();

    //开盘
    $scope.handleStop = () => {
      this.stopPred();
    }

    //仅能修改一次.
    $scope.handleEdit = () =>{
      $scope.can_edit = true;
      $scope.editRight = false;
    }

    $scope.handleEditCancel = () => {
      $scope.can_edit = false;
    }

    //流盘
    $scope.handleCancel = () => {
      if(confirm("确认流盘该预测吗？")){
        this.cancelPred();
      }
    }

    //开奖
    $scope.handleFinishPred = (option) => {
      if(!option){
        alert("必须选择一项才能开奖!");
        return;
      }
      let name = $scope.params["option"+ option];
      if(confirm(`开奖选项: [${name}]`)){
        this.finishPred({option});
      }
    }

    //提交
    $scope.handleSubmit = (params = {}, form) => {
      if(!params.option1 || !params.option2){
        alert("预测选项必须填写!");
        return;
      }
      this.submit(params, form);
    }
  }
  /**
   * 获得预测状态
   */
  getPred(){
    DataService.get('/zhubo/topic')
      .then(data=>{
        if(data.code == 1 && _.isObject(data.data) && data.data.id){

          this.$scope.$apply(() => {
            this.$scope.params.option1 = data.data.option1;
            this.$scope.params.option2 = data.data.option2;
            this.$scope.params.score = data.data.score;
            this.$scope.params.stop_time = data.data.stop_time || new Date();
            this.$scope.params.id = data.data.id;
            this.$scope.right_option = data.data.right_option;
            this.$scope.status = +data.data.status;
            this.$scope.can_edit = false;
            this.$scope.editRight = data.data.can_edit;
            this.$scope.isCreate = false;
            this.$scope.autoPred = !!data.data.stop_time;
            this.$scope.finishTime = data.data.stop_time;
          })

        }

      })
  }
  /**
   * 封盘
   */
  stopPred() {
    DataService.put('/zhubo/topic/stop')
      .then(data=>{
        if(data.code == 1){
          this.$scope.$apply(() => {
            this.$scope.can_edit = false;
            this.$scope.isCreate = false;
            this.$scope.status = 1;
          })

        }

      })
  }
  /**
   * 流盘
   */
  cancelPred() {
    DataService.put('/zhubo/topic/cancel')
      .then(data=>{
        if(data.code == 1){
          this.$scope.$apply(() => {
            this.$scope.can_edit = false;
            this.$scope.isCreate = false;
            this.$scope.status = 3;
          })

        }

      })
  }
  /**
   * 开奖
   */
  finishPred(params) {
    DataService.put('/zhubo/topic/finish', params)
      .then(data=>{
        if(data.code == 1){
          this.$scope.$apply(() => {
            this.$scope.can_edit = false;
            this.$scope.isCreate = false;
            this.$scope.status = 2;
            this.$scope.right_option = params.option;
          })

        }

      })
  }
  /**
   * 发送
   * @return {[type]} [description]
   */
  submit(params, form) {

    if(!form.$valid){
      return;
    }
    let request = '/zhubo/topic';
    let method = "post";

    if(!this.$scope.autoPred){
      delete params.stop_time;
    }

    if(params.id === 0){
      delete params.id;
    }else{
      request = `${request}/${params.id}`;
      method = "put";
    }

  	DataService[method](request, params)
      .then((data) => {
        if(data.code === 1){
          this.getPred();
          return;
        }

        this.$scope.$apply(() => {
          this.$scope.error = data.msg;
        })

        this.$timeout(() => {
          this.$scope.error = '';
          this.$scope.can_edit = false;
        }, 2000)
      });
  }
}

Controller.$inject = ['$scope', '$timeout', 'cachePool'];

export default Controller;



